import React from 'react';
import autoBind from 'react-autobind';
import axios from 'axios';
import { api } from '../../functions/api.js';
import { auth, checkLogin } from '../../functions/auth.js';

class Email extends React.Component {
  constructor(props) {
    super(props);
    autoBind(this);
    this.state = {
      islogin: false,
    };
    auth(this);
  }

  sendEmailAction(e) {
    //阻止表单提交事件
    e.preventDefault()
    // api('auth/login')
    axios.post(api('auth/password/reset/email'), {
      email: this.refs.email.value,
    }).then((response) => {
      if (response.data.code === 200) {
        alert("邮件发送成功，请查收");
        //正确完成后路由跳转
        window.location.href = "/";
      } else if (response.data.code === 500) {
        alert(response.data.message);
      } else {
        alert('something worng');
      }
    }).catch((error) => {
      let errorMsg = '';
      errorMsg += error.response.data.message + '\n';
      Object.keys(error.response.data.errors).forEach((key) => {
        errorMsg += error.response.data.errors[key] + '\n';
      });
      alert(errorMsg);
      console.log(error);
    });
    return false;
  }

  render() {
    return (
      <div className="row">
        {checkLogin(this)}
        <div className="col-md-6 col-md-offset-3">
          <h4>找回密码</h4>

          <div className="panel panel-default">
            <div className="panel-body form-margin">
              <form onSubmit={this.sendEmailAction}>

                <div className="form-group">
                  <label htmlFor="email" className="control-label">邮箱</label>
                  <input name="email" type="email" className="form-control" ref="email" required="required" />
                </div>

                <div className="form-group">
                  <button type="submit" className="btn btn-success">
                    发送密码重置邮件
                  </button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export { Email }


